import React, { useState } from 'react';
// import { Button, Finish } from 'antd';
import Title from './Component/Title';
import Select from './Component/Select';
import Next from './Component/Next';
import Finish from './Component/Finish';
import Result from './Component/Result';

function App() {
  const [member, setMember] = useState(2);// 
  const [mode, setMode] = useState(0); //
  const [now, setNow] = useState(1); //  
  const [choose, setChoose] = useState(0); //  

  switch(mode) {
    case(0): //main
      return <div><Title member={member} setMember = {setMember} setMode ={setMode}></Title></div>
    case(1): //
      return <div><Select now ={now} setNow = {setNow} member = {member} choose = {choose} setChoose ={setChoose} setMode = {setMode}  ></Select></div>
    case(2): //  
      return  <div><Next now ={now} setNow = {setNow} setMode ={setMode} member = {member}></Next></div>
    case(3): //
      return  <div><Finish setMode={setMode}></Finish></div>
    case(4): //
      return <div><Result setChoose ={setChoose} setNow = {setNow} setMode={setMode}  member = {member} choose = {choose}></Result></div>
    default:
      return <div><Title member={member} setMember = {setMember} setMode ={setMode}></Title></div>
  }
}

export default App;
